//Versão Longa
export class School {
  //readonly não pode ser alterado
  public readonly name: string
  private readonly Studentes: Student[] = []
  protected readonly cnpj: string
  constructor(name: string, cnpj: string) {
    this.name = name
    this.cnpj = cnpj
  }

  addStudent(Student: Student): void {
    this.Studentes.push(Student)
  }

  getStudentes(): void {
    for (const Student of this.Studentes) {
      console.log(Student)
    }
  }
}
//versão curta
export class Student {
  constructor(public readonly nome: string, public readonly sobrenome: string) {}
}

const school1 = new School('Listoner', '11.111.111/0001-11')
const student1 = new Student('Ane', 'Silv')
const student2 = new Student('Bill', 'Lib')
const student3 = new Student('John', 'Nho')
school1.addStudent(student1)
school1.addStudent(student2)
school1.addStudent(student3)
console.log(school1)
school1.getStudentes()
